html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

h1 {
  font-family: var(--font-antique);
  font-weight: 500;
  color: var(--smokeybrown);
  text-align: center;
}

h2 {
  font-family: var(--font-proxima);
  font-weight: 700;
  color: var(--smokeybrown);
  font-size: 3.5rem;
  text-align: center;
}

h3 {
  font-family: var(--font-proxima);
  font-weight: 700;
  color: var(--dawnyellow);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 3.8125rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 15.3125rem;
  }
}

h3 {
  font-size: 1.375rem;
}
@media (min-width: 71.875em) {
  h3 {
    font-size: 2.3125rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --lightyellow: hsl(34, 57%, 60%);
  --dawnyellow: hsl(34, 56%, 54%);
  --goldenyellow: hsl(35, 47%, 50%);
  --darkyellow: hsl(40, 43%, 40%);
  --smokeybrown: hsl(20, 51%, 10%);
}

:root {
  --font-proxima: "proxima-nova", sans-serif;
  --font-antique: "antique-olive", sans-serif;
}

.container {
  padding: 0 1.4375rem;
  max-width: 103.75rem;
  margin: 0 auto 2.9375rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 2.5rem 8.125rem 0;
  }
}

.card {
  padding: 1.25rem;
}

.button__button {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: var(--dawnyellow);
}

@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
nav {
  overflow: hidden;
  width: 100%;
  font-family: var(--font-proxima);
  font-size: 1rem;
  font-weight: 400;
  color: var(--goldenyellow);
}
nav li {
  border-bottom: 0.125rem solid var(--goldenyellow);
}
nav a {
  color: var(--goldenyellow);
  text-decoration: none;
  margin-left: 3.125rem;
  transition: padding-bottom 1s;
}
nav a:before {
  content: "";
  position: absolute;
  top: 0;
  left: 100%;
  width: 0;
  height: 100%;
  border-bottom: 2pxsolid #000;
  transition: 0.2salllinear;
}

.landing {
  background-color: whitesmoke;
}
.landing img {
  width: 352px;
  height: auto;
  transform: translateX(-5.5px);
  position: absolute;
}
.landing__circle-bg {
  width: 340px;
  height: 340px;
  background-color: var(--darkyellow);
  border-radius: 50%;
  position: absolute;
  display: inline-block;
}
.landing__circle-bg :hover {
  background-color: aqua;
}
.landing p {
  font-family: var(--font-proxima);
  font-weight: 400;
  font-size: 5rem;
  color: var(--dawnyellow);
}